@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh}body{background-color:#000;cursor:default}

.nav-elements{margin:auto;color:#fff;text-decoration:none}.nav-elements:hover{color:rgba(225,6,0,0.7)}.active{color:rgba(225,6,0,0.7)}

.Nav{background-color:transparent;justify-content:center;padding:0 20%;padding-top:24px;font-family:'Lato', sans-serif}

#about{display:flex;height:100vh;flex-basis:auto}@media (max-width: 768px) and (min-width: 320px){#about{margin-top:50px;flex-wrap:wrap}}

#intro{display:flex;width:100%;flex-direction:column;margin:auto 0;margin-left:10%;font-family:PT Sans;font-size:4rem;color:#fff}#intro .typewriter{display:flex}#intro .typewriter .designation{margin-left:20px;min-width:340px;font-family:Roboto Slab;color:rgba(225,6,0,0.7)}.name{padding:0 10px;background-color:#fff;border-radius:20px;font-family:Merriweather;color:#000;transition:all 0.2s ease-in}.name:hover{color:#fff;background-color:rgba(225,6,0,0.7)}

#photo{display:flex;align-items:center;width:100%;margin:0 0;padding:0 0;overflow-y:hidden;overflow-x:hidden}#photo img{height:100vh;margin:auto}

